.sidebar {
  position: fixed;
  grid-column: 1;
  width: 20%;
  height: 100vh;
  background-color: var(--prim-color);
  text-align: center;
}

.sidebar li {
  padding: 10px;
  font-size: 1.5rem;
  border-bottom: 1px groove #8d5919;
}

.sidebar li:hover {
  transition: all 0.3s;
  background-color: #f17105;
}
.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar .nav-Link {
  text-decoration: none;
  color: rgb(252, 245, 245);
}

.sidebar .logout {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.sidebar .brand {
  font-size: 2.2rem;
}
